  <template>
  <div class="navbar_menu">
     <div></div>
      <div class="account_main">
            <div class="navbar_profile">
              <div class="notifications_main">

              </div>
              <div class="user_drop_down">
                <div class="user_images" @click="isActive=!isActive">
                  <img src="../assets/images/image18.png" alt=""  >
                </div>
                <div class="main_dropdown"  :class="{'active':isActive}">
                  <div class="main_name_f">
                    <div>
                      <img src="../assets/images/image18.png" alt="">
                    </div>
                    <div class="user_name_f">
                      <div class="user_name">
                        {{data[0].firstName}}  {{data[0].lastName}}
                      </div>
                      <div>
                        {{data[0].email}}
                      </div>
                    </div>
                  </div>
                  <div class="main_nav_link">
                    <div class="log_out">
                      <button @click="sign_out()">
                        Sign Out
                      </button>
                    </div>
                  </div>

                </div>
              </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      isActive:false,
      isActive1:false,
      data:[]
    }
  },
  methods:{
    sign_out(){
      localStorage.removeItem('token');
      this.$router.push('/login')
    }
  },
  mounted() {
    this.$http.get('admin/get-me/',{
      headers:{
        Authorization:`Bearer ${localStorage.getItem('token')}`
      }
    })
        .then(res=>{
          this.data=res.data
        })
  }
}
</script>
