  <template>
    <div class="home">
      <Sidebar></Sidebar>

      <section class="main_section">
        <div class="main_container">

          <div class="section-2">
              <div class="d-flex justify-content-between">
                <div class="dash_card_title">
                  Coins
                </div>
                <div>
                  <button class="btn btn-primary" @click="$router.push('/coins/create')"> Create</button>
                </div>
              </div>
            <div class="main_cards">
              <table >
                <thead>
                <tr class="border p-2" >
                  <th class="fr_table">id</th>
                  <th>Name</th>
                  <th>Icon</th>
                  <th>Symbol</th>
                  <th>Action</th>
                </tr>
                </thead>
                <tbody >
                <tr v-for="item in data " class="border p-2"  :key="item">
                  <td class="border p-2" >
                    <div>
                       {{item.id}}
                    </div>
                  </td>
                  <td class="border p-2" >
                    <div>
                      {{item.name}}

                    </div>
                  </td>
                  <td class="border p-2" >
                    <div>
                      <img :src="item.icon" width="30px" height="30px" alt="">


                    </div>
                  </td>
                  <td class="border p-2" >
                    <div>
                      {{item.symbol}}
                    </div>
                  </td>
                  <td class="d-flex border p-2" >
                    <div>
                      <button @click="$router.push(`/coins/edit/${item.id}`)" class="mx-2 my-2 btn btn-100 btn-primary">
                        Edit
                      </button>
                    </div>
                    <div>
                      <button @click="deletes(item.id)" class="btn my-2 btn-100 btn-danger">
                        Delete
                      </button>
                    </div>
                  </td>
                </tr>

                </tbody>
              </table>
            </div>
          </div>
        </div>

      </section>
    </div>
  </template>
  <script>
  import Sidebar from "@/components/Sidebar.vue";
  import 'vue-slick-carousel/dist/vue-slick-carousel.css'
  // optional style for arrows & dots
  import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
  export default {
    components:{
      Sidebar,
    },
    data(){
      return{
        data:[]
      }
    },
    mounted() {
      this.$http.get('admin/coins/',{
        headers:{
          Authorization:`Bearer ${localStorage.getItem('token')}`
        }
      })
      .then(res=>{
        this.data=res.data
      })
      // C
    },
    methods:{

      deletes(item){
        let isBoss = confirm("You really want to delete?");
        if (isBoss == true) {
          this.$http.delete(`/admin/coins/${item}/`,
              {
                headers: {
                  'Authorization': 'Bearer ' + localStorage.getItem('token'),
                  'Accept-Language': 'uz-latin'
                }
              })
              .then(()=>{

                this.$toast("Delete", {
                  timeout: 2000,
                  type:"success"
                });
                this.$http.get('admin/coins/',{
                  headers:{
                    Authorization:`Bearer ${localStorage.getItem('token')}`
                  }
                })
                    .then(res=>{
                      this.data=res.data
                    })
              })

        }
      }
    }
  }
  </script>
